export default {
	data() {
		return {
			loadModal:false,
			username:null,
			password:null
			
		}
	},
	methods: {
		login()
		{
			// this.loadModal = true
			// uni.showToast({
			// 	title: this.username + this.password,
			// 	duration: 2000
			// })
			this.loadModal = true;
			var me = this;
			
			uni.setStorageSync("username",this.username)
			uni.setStorageSync("password",this.password)
			uni.request({
				url: me.serverUrl + '/login/mobile' ,
				method: 'POST',
				header:{
					"content-type":'application/x-www-form-urlencoded'
				},
				data: {
					username:this.username,
					password:this.password
				},
				success: res => {
					me.loadModal = false
					if(res.statusCode==404)
					{
						uni.showToast({
							title: "服务器超时",
							duration:2000
						})
					}
					
					
					if(res.data.status==200)
					{
						//登录成功
						uni.setStorageSync('userInfo',res.data.data)
						uni.navigateBack({
							delta: 1
						})
					}else
					{
						this.password = ''
						uni.showToast({
							title:res.data.msg,
							duration: 2000
						})
					}
					
					console.log(res.data)
				},
				fail: () => {
					console.log("出错了")
				},
				complete: () => {}
			});
			
			
		},
		
		
	},
	components:{
		
	}
}
